<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  mounted () {
    document.querySelector('.body-loading').style.display = 'none'
    let list = this.$store.getters.getLanList
    let lang = 'zh'
    setTimeout(() => {
      // 修改跳转的默认路径, 还有当前语言图片路径
      let path = this.$route.path.split('/')[1]
      list.some(element => {
        if (element.lang === path) {
          lang = path
          return true
        }
        path = ''
      })
      this.$store.dispatch ({
        type: 'setLanguage',
        language: lang
      })
      this.$store.dispatch ({
        type: 'setPath',
        path: path
      })
    }, 200) 
  }
}
</script>